'use client';

import { useEffect, useState, useCallback } from 'react';
import { arduinoService } from '@/lib/arduino';
import { Button } from '@/components/ui/button';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Card, CardHeader, CardTitle, CardContent } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';

export function ArduinoControl() {
  const [isConnected, setIsConnected] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [hotkey, setHotkey] = useState('F2');
  const [isHotkeyEnabled, setIsHotkeyEnabled] = useState(false);
  const [isSettingHotkey, setIsSettingHotkey] = useState(false);
  const [sensitivity, setSensitivity] = useState(10);

  // Connect to Arduino
  const handleConnect = async () => {
    try {
      if (!navigator.serial) {
        setError('Web Serial API is not supported in this browser. Please use Chrome or Edge.');
        return;
      }

      setError('Please select your Arduino device from the popup...');
      const success = await arduinoService.connect();
      
      if (success) {
        setIsConnected(true);
        setError(null);
      } else {
        setError('Failed to connect to Arduino. Make sure it is properly connected and try again.');
        setIsConnected(false);
      }
    } catch (err: any) {
      if (err.name === 'NotFoundError') {
        setError('No Arduino device was selected. Please try again and select a device.');
      } else if (err.name === 'SecurityError') {
        setError('Permission to access Arduino was denied. Please grant permission and try again.');
      } else {
        setError(`Error connecting to Arduino: ${err.message || 'Unknown error'}`);
      }
      setIsConnected(false);
    }
  };

  // Disconnect from Arduino
  const handleDisconnect = async () => {
    try {
      const success = await arduinoService.disconnect();
      setIsConnected(!success);
      setError(success ? null : 'Failed to disconnect from Arduino');
    } catch (err) {
      setError('Error disconnecting from Arduino');
    }
  };

  // Handle hotkey setting
  const handleSetHotkey = () => {
    setIsSettingHotkey(true);
    setError('Press any key to set as hotkey...');
  };

  // Handle key press for hotkey setting
  const handleKeyPress = useCallback((event: KeyboardEvent) => {
    if (isSettingHotkey) {
      event.preventDefault();
      setHotkey(event.key.toUpperCase());
      setIsSettingHotkey(false);
      setError(null);
    } else if (isHotkeyEnabled && event.key.toUpperCase() === hotkey && isConnected) {
      // Send movement command when hotkey is pressed
      arduinoService.sendMovement(sensitivity, sensitivity)
        .catch(err => setError('Failed to send movement command'));
    }
  }, [isSettingHotkey, isHotkeyEnabled, hotkey, isConnected, sensitivity]);

  // Set up key press listener
  useEffect(() => {
    window.addEventListener('keydown', handleKeyPress);
    return () => {
      window.removeEventListener('keydown', handleKeyPress);
    };
  }, [handleKeyPress]);

  return (
    <Card className="w-[350px]">
      <CardHeader>
        <CardTitle>Arduino Control Panel</CardTitle>
      </CardHeader>
      <CardContent className="space-y-4 p-6">
        {error && (
          <Alert variant={error.includes('Please select') ? 'default' : 'destructive'} className="mb-4">
            <AlertDescription>{error}</AlertDescription>
          </Alert>
        )}

        <div className="flex justify-between items-center">
          <Label>Connection Status</Label>
          <Button 
            onClick={isConnected ? handleDisconnect : handleConnect}
            variant={isConnected ? "destructive" : "default"}
            className="w-28 transition-all duration-200 hover:scale-105"
          >
            {isConnected ? 'Disconnect' : 'Connect'}
          </Button>
        </div>

        <div className="space-y-2">
          <Label>Hotkey Settings</Label>
          <div className="flex items-center space-x-2">
            <Input 
              value={hotkey}
              readOnly
              className="w-24"
            />
            <Button 
              onClick={handleSetHotkey}
              disabled={!isConnected}
              variant="outline"
            >
              Set Hotkey
            </Button>
          </div>
        </div>

        <div className="flex items-center space-x-2">
          <Label>Enable Hotkey</Label>
          <Switch
            checked={isHotkeyEnabled}
            onCheckedChange={setIsHotkeyEnabled}
            disabled={!isConnected}
          />
        </div>

        <div className="space-y-2">
          <Label>Movement Sensitivity: {sensitivity}</Label>
          <Input
            type="range"
            min="1"
            max="100"
            value={sensitivity}
            onChange={(e) => setSensitivity(Number(e.target.value))}
            disabled={!isConnected}
            className="w-full"
          />
        </div>

        <div className="text-sm text-gray-500">
          {isConnected ? (
            <p>Status: Connected and ready</p>
          ) : (
            <p>Status: Disconnected</p>
          )}
        </div>
      </CardContent>
    </Card>
  );
}
